import { ServerEndpoint, sessionId } from "./misc"
import { Message } from 'react-chat-window'
import { WsMessage } from 'src/Types'

const WsCheck = '2kjn427w 45qe83'


export const initWebsocket = () => {
  try {
    const HOST = ServerEndpoint.replace(/^http/, 'ws')
    return  new WebSocket(HOST)
  } catch (error) { return }
}
export const sendWsMessage = (socket: WebSocket, message: Message) => {
  let wsMessge : WsMessage = {
    sessionId,
    check: WsCheck,
    message,
  }
  sendMessage(wsMessge, socket)
}
export const sendWsHiMessage = (socket: WebSocket) => {
  let wsMessge : WsMessage = {
    sessionId,
    check: WsCheck,
    action: 'hi',
  }
  sendMessage(wsMessge, socket)
}

const sendMessage = (wsMessge: WsMessage, socket: WebSocket) =>{
  try {
    waitForSocketConnection(socket, () => {
      socket.send(JSON.stringify(wsMessge))
    })
  } catch(e){ return }
}

const waitForSocketConnection = (socket: WebSocket, callback: () => void) =>{
  setTimeout(
    () => {
      if (socket.readyState === 1) {
        if (callback != null) {
          callback()
        }
      } else {
        waitForSocketConnection(socket, callback)
      }

    }, 50)
}


